            transform: scale(1.05);
  }

  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  75% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {

