.subOrder {
  position: absolute;
  top: 50px;
  right: -104px;
  width: 270px;
  height: 200px;
  background-color: #FDFFFC;
  z-index: 100;
  cursor: auto;
  padding: 30px;
  overflow-y: scroll;
  display: none;
  color: #000;

}

.subOrder.subActive {
  display: block;
}

.subOrder ul li {
  font-size: 12px;
}

.subOrder ul li:nth-child(2n) {
  background-color: #E5E5E5;
}

.subOrder ul li .itemPrice {
  font-size: 12px;
}

.subOrder ul li .itemQuantity {
  font-size: 12px;
}

.subOrder h3.headerInfo,
.subOrder h3 {
  font-size: 18px;
  color: #000;
}

.subOrder h3 {
  margin-top: 10px;
}

.subOrder p {
  font-size: 12px;
}

.subOrder .loginInfo {
  font-size: 14px;
  margin-top: 25px;
  font-weight: bold;
  background-color: #00A8E8;
  color: #e84900;
  padding: 5px;
}

.subOrder .loginInfo a {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
}


.mainOrder {
  display: flex;
  flex-direction: column;
  align-content: center;
  font-size: 20px;
}

.mainOrder ul {
  list-style-type: none;
  display: flex;
  margin: 50px;
  flex-direction: column;
}

.subOrder ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mainOrder li:nth-child(2n) {
  background-color: #fff;
}

.mainOrder li {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.mainOrder ul span {
  color: #000;
  font-size: 20px;
}

.mainOrder h3 {
  text-align: center;
}

.mainOrder .orderButton,
.subOrder .orderButton {
  background-color: #E71D36;
  width: 200px;
  font-weight: bold;
  color: white;
  padding: 12px 12px;
  margin: 8px auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-transform: uppercase;
}

.orderButton span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.orderButton span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: -2px;
  right: -20px;
  transition: 0.5s;
}

.orderButton:hover span {
  padding-right: 25px;
}

.orderButton:hover span:after {
  opacity: 1;
  right: 0;
}

.mainOrder .loginInfo {
  padding: 15px;
  margin-top: 25px;
  font-weight: bold;
  background-color: #00A8E8;
  color: #e84900;
}

.mainOrder .loginInfo a {
  color: #fff;
  text-decoration: none;
}


.successMsg {
  background: url('../images/success.jpeg') no-repeat center center;
  background-size: cover;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100vh;
  z-index: 10000;
}


@media (min-width: 400px) {
  .subOrder {
    right: 23px;
  }

}